import React from 'react';
import PropTypes from 'prop-types';
import ClassicInput from "./input";
import FormMessage from "./message";
import SelectInput from "./select";
import TextareaInput from "./textarea";


class Form extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            fieldClass: props.data.fieldClass || null,
            labelClass: props.data.labelClass || null,
            inputClass: props.data.inputClass || null,
            formData: {}
        };
    }

    componentDidMount() {
        const {data} = this.props;
        const state = {};

        data.fields.forEach((field) => {
            const {type, value, name} = field;
            if(type !== "submit") {
                state[name] = value || "";
            }
        });

        this.setState(state);
    }

    handleInputChange = (e, state) => {
        const {name, value} = state;
        this.setState({formData: {
            ...this.state.formData,
            [name]: value
        }});
    };

    createClassicInput = (i, commonProps, fieldProps) => {
        const props = {
            ...commonProps,
            onChange: this.handleInputChange,
            ...fieldProps
        };

        return <ClassicInput key={i} {...props} />
    };

    createSelectInput = (i, commonProps, fieldProps) => {
        const { type, ...inputProps } = fieldProps;
        
        const props = {
            ...commonProps,
            onChange: this.handleInputChange,
            ...inputProps
        };

        return <SelectInput key={i} {...props} />
    };
    
    createTextareaInput = (i, commonProps, fieldProps) => {
        const { type, ...inputProps } = fieldProps;

        const props = {
            ...commonProps,
            onChange: this.handleInputChange,
            ...inputProps
        };

        return <TextareaInput key={i} {...props} />
    };

    createMessage = (field, i) => {
        const { fieldClass, label, ...inputProps } = field;
        const { name } = field;

        const props = {
            label,
            fieldClass: fieldClass || this.state.fieldClass,
            labelClass: this.state.labelClass,
            inputClass: this.state.inputClass,
            id: `${name}_${i}`,
            ...inputProps
        };

        return <FormMessage key={i} {...props} />
    };

    createFields = (data) => (
        data.fields.map(
            (field, i) => {
                const { fieldClass, label, className, required, ...fieldProps} = field;
                const {name} = field;

                const props = {
                    label,
                    fieldClass: fieldClass || this.state.fieldClass,
                    labelClass: this.state.labelClass,
                    className: className || this.state.inputClass,
                    required: required !== undefined ? required : data.allFieldsRequired,
                    id: `${name}_${i}`,
                    autoComplete: "off"
                };

                switch (field.type) {
                    case 'message': return this.createMessage(field, i);
                    case 'textarea': return this.createTextareaInput(i, props, fieldProps);
                    case 'select': return this.createSelectInput(i, props, fieldProps);
                    default: return this.createClassicInput(i, props, fieldProps);
                }
            }
        )
    );

    handleSubmit = (e) => {
        e.preventDefault();
        const {onSubmit} = this.props;
        onSubmit(e, this.state.formData);
    };

    render() {
        const {
            data, children, ...formProps
        } = this.props;

        return (
            <form {...formProps} onSubmit={this.handleSubmit}>
                {children}
                {this.createFields(data)}
            </form>
        );
    }
};

Form.defaultProps = {
    data: {}
};

Form.propTypes = {
    data: PropTypes.shape({
        allFieldsRequired: PropTypes.bool,
        fieldClass: PropTypes.string,
        labelClass: PropTypes.string,
        inputClass: PropTypes.string,
        fields: PropTypes.arrayOf(PropTypes.shape({
            type: PropTypes.oneOf(['text', 'password', 'email', 'number', 'date', 'select', 'textarea', 'message', 'radio', 'submit', 'reset']).isRequired,
            name: PropTypes.string,
            value: PropTypes.string,
            required: PropTypes.bool,
            autoComplete: PropTypes.string,
            className: PropTypes.string,
            
            fieldClass: PropTypes.string,
            label: PropTypes.shape({
                value: PropTypes.string.isRequired,
                className: PropTypes.string
            }),
            options: PropTypes.arrayOf(PropTypes.shape({
                value: PropTypes.string,
                label: PropTypes.string
            }))
        })).isRequired
    }).isRequired
};

export default Form;