import React from 'react';
import Form from 'components';
import ReactJson from 'react-json-view';


class App extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      inputData: {}
    };
  }

  handleSubmit = (e, data) => {
    this.setState({inputData: data});
  };

  getFormData = () => {
    return {
      allFieldsRequired: true,
      fieldClass: "form_field",
      labelClass: "",
      inputClass: "",
      fields: [
        {
          name: "name",
          type: "text",
          label: {
            value: "Full name"
          }
        },
        {
          name: "Language",
          type: "select",
          label: {
            value: "Language"
          },
          options: [
            { value: "es", label: "Espa√±ol" },
            { value: "en", label: "English" }
          ]
        },
        {
          name: "gender",
          type: "radio",
          value: "m",
          label: {
            value: "Male"
          },
          fieldClass: "d"
        },
        {
          name: "gender",
          type: "radio",
          value: "f",
          label: {
            value: "Female"
          },
          fieldClass: "d"
        },
        {
          name: "email",
          type: "email",
          placeholder: "@",
          label: {
            value: "Email"
          }
        },
        {
          name: "password",
          type: "password",
          label: {
            value: "Password"
          }
        },
        {
          name: "submit",
          type: "submit",
          value: "Send",
          fieldClass: "form_button_field"
        }
      ]
    };
  };

  render() {
    const {inputData} = this.state;
    const formData = this.getFormData();
    const reactJsonOptions = {
      iconStyle: "triangle",
      indentWidth: 4,
      collapsed: false,
      displayObjectSize: false,
      displayDataTypes: false,
      enableClipboard: false
    };

    return (
      <main className="app">
        <section>
          <ReactJson
            src={formData}
            {...reactJsonOptions}
          />
        </section>
        <Form
          data={formData}
          className="form"
          onSubmit={this.handleSubmit}
          >
          <h1 className="form-title">React json to form</h1>
        </Form>
        <section>
          <ReactJson
            src={inputData}
            {...reactJsonOptions}
          />
        </section>
      </main>
    );
  }
}

export default App;